html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #121212;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 150rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

html {
  scroll-behavior: smooth;
}

.bodyIndex {
  background-position: top;
  height: 100vh;
  width: 500vw;
  position: relative;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  overflow-y: hidden;
}
@media (max-width: 900px) {
  .bodyIndex {
    width: 100vw;
    height: 500vh;
  }
}

.background {
  background: url("/static/images/wall-08.webp") 50% 0 no-repeat;
  background-position: top;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  top: 0;
}

.header {
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100vw;
  top: 0;
  left: 0;
}
@media (max-width: 900px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
}
.header .topbar {
  border: 1px solid #555;
  border-radius: 1rem;
  margin: 1rem 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 0;
}
@media (max-width: 900px) {
  .header .topbar {
    width: 100%;
    margin: 0.5rem;
  }
}
.header .topbar p {
  margin: 0 0.3rem;
  color: #ffffff;
  font-family: "Chakra Petch", sans-serif;
  z-index: 1;
}
.header .place {
  width: 30%;
}
.header .place p {
  margin: 0 2rem;
  font-size: 1rem;
  color: #ffb956;
}
.header .place .current {
  font-size: 2rem;
  color: #677eca;
}
@media (max-width: 900px) {
  .header .place {
    width: 95%;
    padding: 1rem;
  }
}
.header .home {
  width: 10%;
  cursor: pointer;
}
.header .home p {
  color: #677eca;
}
@media (max-width: 900px) {
  .header .home {
    width: 95%;
  }
}
.header .menu {
  display: flex;
  width: 100%;
}
.header .menu .blog-contact {
  cursor: pointer;
  justify-content: space-evenly;
}
.header .menu .about {
  cursor: pointer;
  width: 100%;
}
.header .menu .projects {
  cursor: pointer;
  width: 100%;
}
.header .menu .skills {
  cursor: pointer;
  width: 100%;
}
.header .menu .blog-contact {
  width: 100%;
  cursor: pointer;
}

.terminal {
  display: none;
  margin: 0 auto;
  width: 8%;
  height: auto;
  top: 7rem;
  left: 5rem;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  cursor: grab;
  border-radius: 1rem;
  z-index: 5;
}
@media (max-width: 900px) {
  .terminal {
    width: 80%;
    height: auto;
    top: 15rem;
  }
}
.terminal .bar {
  width: 100%;
  height: 5rem;
  background-color: #111;
  border-radius: 1rem 1rem 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.terminal .bar p {
  margin: 0 2rem;
  color: #ffffff;
}
.terminal .bar .buttons {
  display: flex;
}
.terminal .bar .buttons p {
  margin: 0 1rem;
  cursor: pointer;
}
.terminal .bar .buttons .close:hover {
  color: #df2b2b;
}
.terminal .bar .buttons .max:hover {
  color: #ffb956;
}
.terminal .bar .buttons .restore:hover {
  color: #71B100;
}
.terminal .bar .buttons .min:hover {
  color: #677eca;
}

.maximize {
  width: 97vw;
  height: 90vh;
  position: absolute !important;
  cursor: auto;
  margin: 0 auto;
  overflow: hidden;
}
@media (max-width: 900px) {
  .maximize {
    height: 80vh;
  }
}
.maximize .details {
  display: inherit;
}
.maximize .readMore {
  display: none;
}
.maximize .terminalContent {
  height: 93%;
  overflow-y: scroll;
}
.maximize .terminalContent::-webkit-scrollbar {
  width: 0.5rem;
  background: #71B100;
  border-radius: 4px;
}
.maximize .terminalContent::-webkit-scrollbar-thumb {
  background: #677eca;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}
.maximize .text p {
  font-size: 2rem;
  margin: 3rem;
}
.maximize .detailsProjects {
  display: flex;
  justify-content: space-around;
  margin: 10rem 0;
}
@media (max-width: 900px) {
  .maximize .detailsProjects {
    flex-direction: column;
    align-items: center;
    margin: 5rem 0;
  }
}
.maximize .detailsProjects a {
  border: #71B100 3px solid;
  background: rgba(255, 255, 255, 0.3);
  width: 45%;
  color: #000;
}
.maximize .detailsProjects a h1 {
  font-family: "Chakra Petch", sans-serif;
  font-weight: 500;
}
@media (max-width: 900px) {
  .maximize .detailsProjects a {
    width: 80%;
    margin: 3rem 0;
  }
}
.maximize .detailsProjects a:hover {
  border: #677eca 3px solid;
}
.maximize .detailSkills {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 5rem 0;
}
@media (max-width: 900px) {
  .maximize .detailSkills {
    flex-direction: column;
    align-items: center;
    margin: 5rem 0;
  }
}
.maximize .detailSkills a {
  border: #71B100 3px solid;
  background: rgba(255, 255, 255, 0.3);
  margin: 3rem 1rem;
  width: 20%;
  align-items: center;
  display: flex;
  justify-content: center;
  color: #000000;
}
.maximize .detailSkills a h1 {
  font-family: "Chakra Petch", sans-serif;
  font-weight: 500;
  font-size: 2.5rem;
}
@media (max-width: 900px) {
  .maximize .detailSkills a {
    width: 80%;
    margin: 3rem 0;
  }
}
.maximize .detailSkills a:hover {
  border: #677eca 3px solid;
}
.maximize .detailSkills .tools {
  display: none;
}
.maximize .websites {
  flex-wrap: wrap;
  gap: 3rem;
  justify-content: center;
}
@media (max-width: 900px) {
  .maximize .websites {
    flex-direction: column;
    align-items: center;
  }
  .maximize .websites .websiteImage {
    width: 80%;
    border: 1px solid #71B100;
    margin: 1rem;
  }
  .maximize .websites .websiteImage:hover {
    border: 1px solid #677eca;
  }
}

.minimize {
  width: auto;
  height: 5vh;
  position: initial !important;
  cursor: auto;
  margin: 1rem;
  bottom: 3rem !important;
  top: initial !important;
}
.minimize .bar {
  border-radius: 1rem;
}
.minimize .terminalContent {
  display: none;
}

.display {
  display: flex;
  scroll-behavior: auto;
  scroll-snap-type: x mandatory;
}
@media (max-width: 900px) {
  .display {
    flex-direction: column;
  }
}

@media (max-width: 900px) {
  .slide {
    height: 100vh;
  }
}

.first {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  align-content: flex-end;
  flex-wrap: wrap;
}

.second {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  align-content: flex-end;
  flex-wrap: wrap;
}

.third {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
  align-content: flex-end;
  flex-wrap: wrap;
}

.fourth {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  overflow: hidden;
