}

.comandLine {
  padding: 0 1rem;
}
.comandLine h3 {
  font-family: "Chakra Petch", sans-serif;
  color: #ffffff;
  margin: 0;
  font-size: 2rem;
  font-weight: 500;
  display: flex;
  flex-wrap: wrap;
}
.comandLine .user {
  color: #71B100;
}
.comandLine .comand {
  color: #ffffff;
  display: flex;
  white-space: nowrap;
  width: 22rem;
  overflow: hidden;
  margin: 0 1rem;
  animation-fill-mode: forwards;
}
.comandLine .simbols {
  color: #677eca;
}
.comandLine .animationComand {
  animation: typing 1.5s steps(22), blink 0.5s infinite step-end alternate;
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.text {
  padding: 1rem;
}
.text p {
  color: #ffffff;
  margin: 0;
  font-size: 2rem;
  font-family: "Chakra Petch", sans-serif;
}
@media (max-width: 900px) {
  .text p {
    font-size: 1.6rem;
  }
}
@keyframes showDiv {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.animationText {
  animation: showDiv 1.5s steps(1);
}

.details {
  display: none;
}

.websites {
  display: none;
}

.datascience {
  display: none;
}

.websiteImage {
  width: 30%;
  border: 1px solid #71B100;
}
.websiteImage:hover {
  border: 1px solid #677eca;
}

.website {
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}
.website a {
  color: #ffffff;
  font-size: 2rem;
}

.websiteImageDescription img {
  padding: 3rem;
}

.toolsSkills {
  display: none;
  flex-direction: column;
}

.contentSkills {
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 0 5rem;
}
.contentSkills .textSkills {
  color: #ffffff;
  font-size: 2rem;
}

.tools {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.tools img {
  width: 15%;
  border-radius: 50%;
  margin: 3rem;
}
@media (max-width: 900px) {
  .tools img {
    margin: 1rem;
    width: 30%;
  }
}

.userBlog {
  color: #71B100;
  text-align: start;
  display: flex;
  margin: 0;
  font-size: 2rem;
  font-weight: 500;
  font-family: "Chakra Petch", sans-serif;
}
.userBlog .comandBlog {
  color: #ffffff;
  display: flex;
  white-space: nowrap;
  width: 22rem;
  overflow: hidden;
  margin: 0 1rem;
  animation-fill-mode: forwards;
}
.userBlog .simbols {
  color: #677eca;
}
.userBlog .animationComand {
  animation: typing 1.5s steps(22), blink 0.5s infinite step-end alternate;
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.textBlog {
  height: 93%;
}
.textBlog .details {
  height: 100%;
}

.contentBlog {
  display: grid;
  grid-template-columns: 1fr 2fr 5fr;
  height: 100%;
}
@media (max-width: 900px) {
  .contentBlog {
    grid-template-rows: 1fr 2fr 5fr;
    grid-template-columns: none;
  }
}
.contentBlog .categoryBlog,
.contentBlog .fileBlog,
.contentBlog .contentFile {
  border: 1px solid #71B100;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}
.contentBlog .categoryBlog p,
.contentBlog .fileBlog p,
.contentBlog .contentFile p {
  border: 1px solid #71B100;
  width: 98%;
  text-align: center;
  color: #677eca;
  margin: 0.5rem;
}
.contentBlog .categoryBlog .select,
.contentBlog .fileBlog .select,
.contentBlog .contentFile .select {
  background-color: #677eca;
  margin: 0.5rem;
  color: #000000;
  width: 98%;
}
.contentBlog .contentFile {
  overflow-y: scroll;
  overflow-x: hidden;
}
.contentBlog .contentFile p {
  color: #ffffff;
  padding: 2rem;
}
.contentBlog .contentFile::-webkit-scrollbar {
  width: 0.5rem;
  background: #71B100;
  border-radius: 4px;
}
.contentBlog .contentFile::-webkit-scrollbar-thumb {
  background: #677eca;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form h2 {
  color: #ffffff;
}
.form form {
  display: grid;
  width: 50%;
  gap: 1rem;
}
@media (max-width: 900px) {
  .form form {
    width: 100%;
  }
}
.form form label {
  color: #ffffff;
  font-size: 2rem;
}
.form form input, .form form textarea {
  border: 2px solid #71B100;
  background: transparent;
  width: 100%;
  font-size: 2rem;
  color: #ffffff;
}
.form form .name {
  grid-column: 1/2;
  grid-row: 2/3;
}
.form form .phone {
  grid-column: 2/3;
  grid-row: 2/3;
}
.form form .email {
  grid-column: 1/3;
}
.form form .message {
  grid-column: 1/3;
}
.form form .button-24 {
  background: #71B100;
  border: 1px solid #71B100;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 800;
  padding: 1rem;
  margin: 3rem;
  text-align: center;
}
.form form .button-24:hover,
.form form .button-24:active {
  background-color: initial;
  background-position: 0 0;
  color: #71B100;
}
.form form .button-24:active {
  opacity: 0.5;
}
.form .tools {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.form .tools img {
  width: 20rem;
  border-radius: 50%;
  margin: 3rem;
}
@media (max-width: 900px) {
  .form .tools img {
    margin: 1rem;
    width: 10rem;
  }
}

