  flex-direction: column;
  align-items: center;
  height: 70%;
  justify-content: center;
}
.bodyAdmin .terminalContent .text .options h2 {
  color: #ffffff;
}
.bodyAdmin .terminalContent .text .options .buttonsOption {
  align-items: center;
